<template>
    <div class="operator-icons">
        <i class="el-icon-view" @click.stop="view"></i>
        <i class="el-icon-edit" @click.stop="edit"></i>
        <i class="el-icon-delete" @click.stop="del"></i>
    </div>
</template>

<script type="text/ecmascript-6">
    export default {
        methods: {
            view() {
                this.$emit('view');
            },
            edit() {
                this.$emit('edit');
            },
            del() {
                this.$emit('del');
            }
        }
    };
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
    .operator-icons
        i
            font-size: 18px
            & + i
                margin-left: 10px
            &:hover
                cursor: pointer
</style>


