<template>
    <el-table
        :data="data"
        :border="border"
    >
        <el-table-column 
            v-for="(column, index) in columns" 
            :key="index"
            :label="column.label"
            :width="column.width"
            :prop="column.prop"
        >
        </el-table-column>

        <el-table-column 
            v-if="operatorCol.show"
            :label="operatorCol.label"
            :width="operatorCol.width">
            <template scope="scope">
                <operator-column
                    @view="view(scope.$index, scope.row, scope.column, scope.store)"
                    @edit="edit(scope.$index, scope.row, scope.column, scope.store)"
                    @del="del(scope.$index, scope.row, scope.column, scope.store)"></operator-column>
            </template>
        </el-table-column>
    </el-table>
</template>

<script type="text/ecmascript-6">
    import OperatorColumn from 'base/operator-column/operator-column';

    export default {
        props: {
            data: {
                type: Array,
                default: []
            },
            border: {
                type: Boolean,
                default: true
            },
            columns: {
                type: Array,
                default: []
            },
            operatorCol: {
                type: Object,
                default: function() {
                    return {};
                }
            }
        },
        methods: {
            view(index, row, column, store) {
                // console.log(index, row, column, store);
                this.$emit('view', index, row, column, store);
            },
            edit(index, row, column, store) {
                this.$emit('edit', index, row, column, store);
            },
            del(index, row, column, store) {
                this.$emit('del', index, row, column, store);
            }
        },
        components: {
            OperatorColumn
        }
    };
</script>


